.App{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;min-height:100%;color:#000;background-color:#ffe123;font-family:'Itim', cursive}.App .logo{font-family:'Great Vibes', cursive;line-height:1;font-size:3em}.App h2{font-family:'Pacifico', cursive}.App .view-page-container{-webkit-transition:all 0.5s;transition:all 0.5s}.App .view-page-container .experience-item:not(:first-child){padding-top:1em;border-top:1px solid #000}.App .view-page-container .firm-img{max-width:250px}.App .slide-in-enter{opacity:0;-webkit-transform:scale(0);transform:scale(0)}.App .slide-in-enter-active{opacity:1;-webkit-transition:all 500ms;transition:all 500ms;-webkit-transform:scale(1);transform:scale(1)}.App .slide-in-exit{opacity:1}.App .slide-in-exit-active{opacity:0;-webkit-transition:all 500ms;transition:all 500ms;width:100%;-webkit-transform:scale(0);transform:scale(0);position:absolute}
